#replenish-energy {
  width: 400px;

  .swiper {
    width: 360px;
  }

  .modal-close {
    position: absolute;
    right: 5px;
    top: 5px;
  }

  .card {
    background-repeat: no-repeat;
    background-size: 450%;
    background-position: top left;

    color: var(--light3-text) !important;
    border-radius: 10px;

    .replenish-energy-title {
      color: var(--white);
      padding: 10px;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
      background: #0000004a;

      font-size: 1.5rem;
      font-weight: 300;
      color: var(--light-text) !important;
    }
    .replenish-energy-subtitle {
      font-size: 1.2rem;
      font-weight: 200;
      color: var(--light2-text) !important;
    }

    .replenish-energy-content {
      padding: 1.2em;
    }

    .energy-container {
      background-color: var(--black);
      border-radius: 20px;
      width: 100%;
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
      .energy-bar {
        padding: 1px 8px 0;
        height: 22px;
        font-size: 0.8em;
        color: white;
        border-top-left-radius: 20px;
        border-bottom-left-radius: 20px;

        &.energy-full {
          border-top-right-radius: 20px;
          border-bottom-right-radius: 20px;
        }
        transition: all 0.5s;
      }
    }

    .energy-item {
      width: 100px;
      height: 100px;
      margin-bottom: 50px;
      border: solid 1px var(--grey2);
      border-radius: 10px;

      background: radial-gradient(circle, rgba(97, 67, 57, 0.5) 0%, rgba(0, 0, 0, 1) 100%);

      text-align: center;

      &:hover {
        border: solid 1px var(--orange2);

        .item-name {
          font-size: 1em !important;
          // color: var(--orange2);
        }
        .item-balance {
          font-size: 1.1em !important;
        }
      }

      .item-name {
        text-transform: uppercase;
        color: antiquewhite;
        font-size: 0.8em !important;
        font-family: monospace;
      }

      .item-balance {
        color: var(--orange);
        font-family: monospace;
        font-size: 0.9em !important;
      }
    }
  }
}
