body {
  #root {
    #app {
      #content-container {
        .characters {
          overflow: visible;
          .stage {
            max-width: 275px;
            box-sizing: border-box;
            margin-top: 6px;

            .lv1-stage-action,
            .lv2-stage-action,
            .lv3-stage-action {
              width: 98%;
            }

            .lv1-stage-action {
              margin-top: -35px;
              height: 30px;
            }

            .lv2-stage-action {
              margin-top: -65px;
              height: 60px;
            }

            .lv3-stage-action {
              margin-top: -95px;
              height: 90px;
            }

            .stage-action {
              .activate-button {
                display: none;
                border: none !important;
                font-size: 0.8em;
              }

              .tooltip {
                .tooltip-text {
                  filter: drop-shadow(1px 2px 3px black);
                  display: none;
                  text-align: center;
                  padding: 10px;
                  border-radius: 6px;
                  position: absolute;
                  z-index: 11000;
                  left: 50%;
                  margin-top: 0px;
                  margin-left: -100px;
                  background-color: #000;
                  border-color: transparent;
                  color: var(--light-text);
                  font-size: 1em;
                  font-weight: 500;
                  word-wrap: break-word;
                  width: 200px;
                  white-space: -moz-pre-wrap !important;
                  white-space: -pre-wrap;
                  white-space: -o-pre-wrap;
                  white-space: -webkit-pre-wrap;
                  white-space: normal;
                }
                .tooltip-text-up {
                  margin-top: -95px;
                }
                &:hover {
                  .tooltip-text {
                    display: block;
                    animation-delay: 0.5s;
                  }
                }
              }
            }

            &:hover {
              .stage-action {
                .activate-button {
                  display: block;
                  width: 100%;
                  background: rgba(0, 0, 0, 0.65);
                  border-bottom-left-radius: 15px;
                  border-bottom-right-radius: 15px;

                  -webkit-animation: scale-up-ver-bottom 0.2s cubic-bezier(0.39, 0.575, 0.565, 1) both;
                  animation: scale-up-ver-bottom 0.2s cubic-bezier(0.39, 0.575, 0.565, 1) both;

                  span {
                    -webkit-animation: text-focus-in 0.4s cubic-bezier(0.55, 0.085, 0.68, 0.53) both;
                    animation: text-focus-in 0.4s cubic-bezier(0.55, 0.085, 0.68, 0.53) both;
                  }
                }

                .card {
                  box-shadow: -4px 5px 15px -3px rgba(0, 0, 0, 0.7);
                }
              }
            }

            .card {
              -webkit-animation: glow 2s infinite alternate;
              animation: glow 2s infinite alternate;
              background-repeat: no-repeat;
              background-size: 450%;
              background-position: top left;
              box-shadow: -4px 5px 15px -3px rgba(0, 0, 0, 0.2);
              border-radius: 15px;

              &.unknown {
                box-shadow: 1px 1px 10px 2px var(--orange2);
              }
              &.unique {
                box-shadow: 1px 1px 10px 2px #ffd700cc;
              }
              &.legendary {
                box-shadow: 1px 1px 10px 2px #ffd700cc;
              }
              &.epic {
                box-shadow: 1px 1px 6px 1px #eeeeeecc;
              }
              &.rare {
                box-shadow: 1px 1px 6px 1px #936d3bcc;
              }
              &::after {
                content: '';
                position: absolute;
                right: -0.1px;
                left: -0.1px;
                top: -0.1px;
                bottom: -0.1px;
                border-radius: 15px;
              }

              .card-image {
                height: 250px;
                width: 250px;
                .image {
                  height: 100%;
                }
              }

              .card-content {
                .content {
                  .character-title {
                    color: var(--light-text);
                    font-size: 1em !important;
                  }
                  .character-boost-remaining {
                    background: url(../../assets/fire.png);
                    background-clip: text;
                    -webkit-background-clip: text;
                    -webkit-text-fill-color: transparent;
                    font-size: 1em;
                    font-weight: 700;
                    opacity: 0.5;
                    padding: 1px;
                    padding-top: 8px !important;
                  }
                  .is-size-6 {
                    font-size: 0.7em !important;
                  }
                  .is-size-7 {
                    font-size: 0.6em !important;
                  }
                  color: var(--light-text);

                  .attributes {
                    height: 28px;
                    padding-top: 3px;

                    .tag {
                      font-size: 0.5em !important;
                      margin-right: 3px;
                      border-radius: 10px;

                      &.Unknown {
                        background: linear-gradient(rgb(209, 160, 0), rgb(167, 97, 0));
                      }
                      &.Special {
                        background: linear-gradient(purple, rgb(59, 1, 59));
                      }
                      &.Image {
                        background: linear-gradient(#188f16, #003000);
                      }
                      &.Stat {
                        background: linear-gradient(var(--red), rgb(56, 5, 1));
                      }
                    }
                  }

                  .stats {
                    div {
                      width: 100%;

                      .super-stat {
                        color: #ff5656;
                        color: var(--red);
                      }

                      .good-stat {
                        color: #caca50;
                        color: var(--action-text);
                      }
                    }
                  }

                  .energy {
                    .energy-container {
                      margin-top: 0.7em;
                      background-color: var(--black);
                      border-radius: 10px;

                      .energy-bar {
                        padding: 1px 8px 0;
                        height: 12px;
                        font-size: 0.5em;
                        color: white;
                        border-top-left-radius: 5px;
                        border-bottom-left-radius: 5px;
                        &.energy-full {
                          border-top-right-radius: 5px;
                          border-bottom-right-radius: 5px;
                        }
                        transition: all 0.5s;
                      }
                    }
                  }
                }
              }

              .image {
                img {
                  border-radius: 10px;
                }

                .nft-sample {
                  color: #ffffff2b;
                  position: absolute;
                  font-size: 4em;
                  top: 80px;
                  left: 15px;
                  -moz-transform: rotate(-45deg) translateX(0px) scale(1);
                  -o-transform: rotate(-45deg) translateX(0px) scale(1);
                  -webkit-transform: rotate(-45deg) translateX(0px) scale(1);
                  transform: rotate(-45deg) translateX(0px) scale(1);
                }
              }
            }
          }
        }

        .modal-background {
          background-color: rgba(10, 10, 10, 0.9);
        }

        .character-boost {
          width: 380px;

          .modal-close {
            position: absolute;
            right: 5px;
            top: 5px;
          }

          .card {
            border: solid 1px var(--bd-grey2);
            border-radius: 10px;

            background-color: #000;
            box-shadow: -4px 5px 15px -3px rgb(0, 0, 0 / 20%);

            padding: 1.3em 1em 1.6em;

            .character-boost-title {
              font-size: 1.5rem;
              font-weight: 300;
              color: var(--light2-text) !important;
            }

            .boosting {
              color: red;
            }

            .character-boost-stats {
              input {
                text-align: center;
              }
              .minus-button {
                border-bottom-left-radius: 0;
              }
              .plus-button {
                border-bottom-right-radius: 0;
              }
              .boost-button {
                margin-top: -1px;
                border-top-left-radius: 0;
                border-top-right-radius: 0;
              }
            }

            .character-stat-list-item {
              button {
                height: 30px;
              }
              &.is-disabled {
                button {
                  opacity: 0.5;
                }
              }
              .boosted {
                color: green;
              }
            }
          }
        }
      }
    }
  }
}
