#begin-quest {
  width: 400px;

  .swiper {
    width: 360px;
  }
  .modal-close {
    position: absolute;
    right: 5px;
    top: 5px;
  }

  .card {
    background-repeat: no-repeat;
    background-size: 450%;
    background-position: top left;

    color: var(--light3-text) !important;
    border-radius: 10px;

    .begin-quest-title {
      color: var(--white);
      padding: 10px;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
      background: #0000004a;

      font-size: 1.5rem;
      font-weight: 300;
      color: var(--light-text) !important;
    }
    .begin-quest-subtitle {
      font-size: 1.2rem;
      font-weight: 200;
      color: var(--light2-text) !important;
    }

    .begin-quest-content {
      padding: 1.2em;
    }

    .character-stats {
      .field {
        margin-bottom: 0;
      }
      input {
        flex-grow: 1;
        text-align: center;
      }
      .minus-button {
        flex-grow: 0;
        border-bottom-left-radius: 0;
      }
      .plus-button {
        flex-grow: 0;
        border-bottom-right-radius: 0;
      }
      .begin-button {
        margin-top: -1px;
        border-top-left-radius: 0;
        border-top-right-radius: 0;
      }
    }

    .character-stat-list-item {
      color: var(--light3-text) !important;
    }

    .equip-item {
      width: 100px;
      height: 100px;
      margin-bottom: 50px;
      border: solid 1px var(--grey2);
      border-radius: 10px;

      background: radial-gradient(circle, rgba(97, 67, 57, 0.5) 0%, rgba(0, 0, 0, 1) 100%);

      text-align: center;

      &:hover {
        border: solid 1px var(--orange2);

        .item-name {
          font-size: 1em !important;
          // color: var(--orange2);
        }
        .item-balance {
          font-size: 1.1em !important;
        }
      }

      .item-name {
        text-transform: uppercase;
        color: antiquewhite;
        font-size: 0.8em !important;
        font-family: monospace;
      }

      .item-balance {
        color: var(--orange);
        font-family: monospace;
        font-size: 0.9em !important;
      }
    }
  }
}
